
// ==================== 渡劫区域样式 ====================
.section-header.tribulation-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--border-color);
  font-size: 15px;
  font-weight: bold;
  color: #8844ff;

  i {
    color: #8844ff;
    animation: lightning-flash 0.5s ease-in-out infinite;
  }
}

.tribulation-overview {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-bottom: 20px;
  padding: 20px;
  background: linear-gradient(135deg, rgba(136, 68, 255, 0.1) 0%, rgba(255, 68, 170, 0.1) 100%);
  border: 2px solid rgba(136, 68, 255, 0.4);
  border-radius: 12px;
  animation: tribulation-glow 2s ease-in-out infinite;

  .tribulation-type-badge {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 20px;
    border-radius: 25px;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    text-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
    animation: badge-pulse 1.5s ease-in-out infinite;

    i {
      font-size: 20px;
      animation: lightning-flash 0.5s ease-in-out infinite;
    }
  }

  .tribulation-level {
    font-size: 16px;
    font-weight: bold;
    padding: 8px 16px;
    background: var(--button-bg);
    border: 1px solid var(--border-color);
    border-radius: 20px;
  }
}

.tribulation-progress-section {
  margin-bottom: 20px;
  padding: 16px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 10px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #8844ff;
    }
  }

  .tribulation-stages {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    font-size: 16px;

    .stage-current {
      color: var(--accent-color);
      font-weight: bold;
    }

    .stage-divider {
      color: var(--text-secondary);
    }

    .stage-total {
      color: var(--text-secondary);
    }
  }

  .tribulation-progress-bar {
    height: 10px;
    background: var(--progress-bg);
    border-radius: 5px;
    overflow: hidden;

    .tribulation-progress-fill {
      height: 100%;
      border-radius: 5px;
      transition: width 0.5s ease;
      box-shadow: 0 0 15px currentColor;
    }
  }
}

.tribulation-endurance-section {
  margin-bottom: 16px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #44aa44;
    }
  }

  .endurance-bar-container {
    display: flex;
    align-items: center;
    gap: 12px;

    .endurance-bar {
      flex: 1;
      height: 14px;
      background: var(--progress-bg);
      border-radius: 7px;
      overflow: hidden;

      .endurance-fill {
        height: 100%;
        border-radius: 7px;
        transition: width 0.5s ease, background-color 0.3s ease;
        box-shadow: 0 0 10px currentColor;
      }
    }

    .endurance-value {
      font-size: 15px;
      font-weight: bold;
      color: var(--text-primary);
      min-width: 55px;
      text-align: right;
    }
  }
}

.protection-section {
  margin-bottom: 16px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #44aaff;
    }
  }

  .protection-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;

    .protection-tag {
      padding: 5px 14px;
      background: rgba(68, 170, 255, 0.15);
      border: 1px solid rgba(68, 170, 255, 0.4);
      border-radius: 15px;
      font-size: 12px;
      color: #44aaff;
    }
  }
}

.tribulation-desc-section {
  margin-bottom: 16px;
  padding: 14px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 10px;

  .section-title {
    font-size: 13px;
    color: var(--text-accent);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #ffc864;
    }
  }

  .tribulation-desc {
    font-size: 13px;
    color: var(--text-secondary);
    line-height: 1.6;
    font-style: italic;
  }
}

.tribulation-cause-section {
  padding: 12px 14px;
  background: rgba(136, 68, 255, 0.1);
  border: 1px dashed rgba(136, 68, 255, 0.4);
  border-radius: 8px;
  display: flex;
  gap: 8px;
  align-items: flex-start;

  .cause-label {
    font-size: 12px;
    color: #8844ff;
    font-weight: 500;
    white-space: nowrap;
  }

  .cause-text {
    font-size: 12px;
    color: var(--text-secondary);
    line-height: 1.5;
  }
}

// 渡劫动画
@keyframes tribulation-glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(136, 68, 255, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(136, 68, 255, 0.6);
  }
}

@keyframes badge-pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes lightning-flash {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

// ==================== 战斗区域样式 ====================

// 战斗状态头部 - 紧凑横向布局
.combat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  margin-bottom: 16px;
  background: linear-gradient(135deg, rgba(255, 100, 100, 0.12) 0%, rgba(200, 80, 80, 0.08) 100%);
  border: 1px solid rgba(255, 100, 100, 0.35);
  border-radius: 12px;
  position: relative;

  // 左侧静态边框
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 4px;
    background: linear-gradient(180deg, #ff6b6b 0%, #ff8888 50%, #ff6b6b 100%);
    border-radius: 4px 0 0 4px;
  }

  .combat-status-left {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .combat-status-indicator {
    width: 42px;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 18px;
    
    &.status-激战, &.status-战斗中 {
      background: rgba(255, 100, 100, 0.2);
      color: #ff6b6b;
    }
    
    &.status-对峙 {
      background: rgba(255, 180, 60, 0.2);
      color: #ffb43c;
    }
    
    &.status-重伤 {
      background: rgba(255, 136, 0, 0.2);
      color: #ff8800;
    }
    
    &.status-濒死 {
      background: rgba(200, 50, 50, 0.25);
      color: #cc3333;
    }

    // 交叉双剑图标 (Unicode)
    .crossed-swords-icon {
      font-size: 20px;
      line-height: 1;
    }
  }

  .combat-status-text {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }

  .combat-status-label {
    font-size: 17px;
    font-weight: bold;
    
    &.status-激战, &.status-战斗中 {
      color: #ff6b6b;
    }
    
    &.status-对峙 {
      color: #ffb43c;
    }
    
    &.status-重伤 {
      color: #ff8800;
    }
    
    &.status-濒死 {
      color: #cc3333;
    }
  }

  .combat-status-desc {
    font-size: 12px;
    color: var(--text-secondary);
    max-width: 180px;
  }

  .combat-round-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px 14px;
    background: rgba(255, 100, 100, 0.1);
    border: 1px solid rgba(255, 100, 100, 0.25);
    border-radius: 10px;

    .round-label {
      font-size: 10px;
      color: var(--text-secondary);
      text-transform: uppercase;
      letter-spacing: 1px;
    }

    .round-number {
      font-size: 20px;
      font-weight: bold;
      color: #ff6b6b;
      line-height: 1.1;
    }
  }
}

@keyframes pulse-danger-text {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}

// 状态一览 - 类似天下太平风格
.combat-stats {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;

  .combat-stat-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 14px 16px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 12px;

    .combat-stat-icon {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      font-size: 18px;

      &.spirit {
        background: rgba(74, 158, 255, 0.15);
        color: #4a9eff;
      }

      &.power {
        background: rgba(255, 170, 100, 0.15);
        color: #ffaa64;
      }

      &.injury-icon-无伤 {
        background: rgba(102, 204, 136, 0.15);
        color: #66cc88;
      }
      
      &.injury-icon-轻伤 {
        background: rgba(255, 204, 0, 0.15);
        color: #ffcc00;
      }
      
      &.injury-icon-重伤 {
        background: rgba(255, 136, 0, 0.15);
        color: #ff8800;
      }
      
      &.injury-icon-濒死 {
        background: rgba(255, 68, 68, 0.15);
        color: #ff4444;
        animation: pulse-danger 1.5s ease-in-out infinite;
      }
    }

    .combat-stat-content {
      flex: 1;

      .combat-stat-label {
        font-size: 12px;
        color: var(--text-secondary);
        margin-bottom: 6px;
      }

      .combat-stat-bar {
        height: 8px;
        background: var(--progress-bg);
        border-radius: 4px;
        overflow: hidden;

        .combat-stat-fill {
          height: 100%;
          border-radius: 4px;
          transition: width 0.5s ease;

          &.spirit {
            box-shadow: 0 0 10px rgba(74, 158, 255, 0.5);
          }
        }
      }

      .combat-stat-value {
        font-size: 13px;
        font-weight: 500;
        color: var(--text-accent);
        margin-top: 4px;
      }

      .combat-power-value {
        display: flex;
        align-items: center;
        gap: 4px;
        margin-top: 2px;
        
        .power-number {
          font-size: 22px;
          font-weight: bold;
          color: #ffaa64;
          text-shadow: 0 0 10px rgba(255, 170, 100, 0.4);
        }
      }

      .combat-stat-status {
        display: inline-flex;
        align-items: center;
        gap: 6px;
        padding: 4px 12px;
        border-radius: 12px;
        font-size: 13px;
        font-weight: 500;

        i {
          font-size: 12px;
        }

        &.status-无伤 {
          background: rgba(68, 170, 68, 0.15);
          color: #44aa44;
        }

        &.status-轻伤 {
          background: rgba(255, 204, 0, 0.15);
          color: #ffcc00;
        }

        &.status-重伤 {
          background: rgba(255, 136, 0, 0.15);
          color: #ff8800;
        }

        &.status-濒死 {
          background: rgba(255, 68, 68, 0.15);
          color: #ff4444;
          animation: pulse-danger 1.5s ease-in-out infinite;
        }
      }
    }
  }
}

// 已用底牌区块
.trump-cards-section {
  margin-bottom: 20px;
  padding: 16px;
  background: linear-gradient(135deg, rgba(255, 200, 100, 0.08) 0%, rgba(255, 180, 80, 0.05) 100%);
  border: 1px dashed rgba(255, 200, 100, 0.3);
  border-radius: 12px;

  .trump-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #ffc864;

    i {
      font-size: 14px;
    }
  }
  
  .trump-cards-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    
    .trump-card-tag {
      padding: 5px 14px;
      background: rgba(255, 200, 100, 0.15);
      border: 1px solid rgba(255, 200, 100, 0.4);
      border-radius: 15px;
      font-size: 12px;
      color: #ffc864;
    }
  }
}

// 敌人区块
.enemies-section {
  .enemies-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    font-size: 14px;
    font-weight: 500;
    color: var(--text-accent);

    i {
      font-size: 14px;
      color: #ff6b6b;
    }
    
    .enemy-count {
      margin-left: auto;
      padding: 2px 10px;
      background: rgba(255, 100, 100, 0.15);
      border-radius: 10px;
      font-size: 12px;
      color: #ff6b6b;
    }
  }
  
  .enemies-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .enemy-card {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    padding: 14px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 12px;
    transition: all 0.3s ease;
    
    &:hover {
      border-color: rgba(255, 100, 100, 0.4);
    }
    
    &.enemy-dead {
      opacity: 0.5;
      
      .enemy-name {
        text-decoration: line-through;
        color: var(--text-secondary);
      }
      
      .enemy-avatar {
        background: rgba(102, 102, 102, 0.15) !important;
        color: #666 !important;
      }
    }
    
    .enemy-avatar {
      width: 44px;
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      font-size: 20px;
      flex-shrink: 0;
      
      &.threat-safe {
        background: rgba(68, 170, 68, 0.15);
        color: #44aa44;
      }
      
      &.threat-easy {
        background: rgba(136, 204, 68, 0.15);
        color: #88cc44;
      }
      
      &.threat-equal {
        background: rgba(255, 204, 0, 0.15);
        color: #ffcc00;
      }
      
      &.threat-hard {
        background: rgba(255, 136, 0, 0.15);
        color: #ff8800;
      }
      
      &.threat-deadly {
        background: rgba(255, 68, 68, 0.15);
        color: #ff4444;
        animation: pulse-danger 1.5s ease-in-out infinite;
      }
    }
    
    .enemy-info {
      flex: 1;
      min-width: 0;
    }
    
    .enemy-header-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
      margin-bottom: 8px;
      
      .enemy-name {
        font-size: 15px;
        font-weight: 600;
        color: #ff8888;
      }
      
      .enemy-realm {
        padding: 2px 10px;
        background: var(--button-bg);
        border: 1px solid var(--border-color);
        border-radius: 10px;
        font-size: 11px;
        color: var(--text-accent);
        flex-shrink: 0;
      }
    }
    
    .enemy-status-row {
      display: flex;
      align-items: center;
      gap: 16px;
      margin-bottom: 6px;
      
      .enemy-power, .enemy-health {
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 12px;
        
        i {
          font-size: 11px;
        }
      }
      
      .enemy-power {
        &.power-safe { color: #44aa44; }
        &.power-easy { color: #88cc44; }
        &.power-equal { color: #ffcc00; }
        &.power-hard { color: #ff8800; }
        &.power-deadly { color: #ff4444; }
        
        .enemy-power-value {
          font-size: 10px;
          opacity: 0.8;
          margin-left: 2px;
        }
      }
      
      .enemy-health {
        &.health-完好 { color: #44aa44; }
        &.health-轻伤 { color: #ffcc00; }
        &.health-重伤 { color: #ff8800; }
        &.health-濒死 { color: #ff4444; }
        &.health-已死 { color: #666666; }
      }
    }
    
    .enemy-trait {
      display: flex;
      align-items: center;
      gap: 6px;
      padding-top: 8px;
      border-top: 1px dashed var(--border-color);
      font-size: 12px;
      color: var(--text-secondary);
      
      i {
        font-size: 10px;
        color: #ffc864;
      }
    }
  }
  
  .empty-hint {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 30px 20px;
    background: var(--bg-secondary);
    border: 1px dashed var(--border-color);
    border-radius: 12px;
    color: var(--text-secondary);
    
    i {
      font-size: 28px;
      color: #66aa88;
    }
    
    span {
      font-size: 13px;
      font-style: italic;
    }
  }
}

@keyframes pulse-danger {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(255, 68, 68, 0.4);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(255, 68, 68, 0);
  }
}

// 非战斗状态 - 太平盛世样式
.peace-state-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 20px;
  margin-bottom: 20px;
  background: linear-gradient(135deg, rgba(68, 170, 68, 0.1) 0%, rgba(100, 200, 150, 0.08) 100%);
  border: 1px solid rgba(68, 170, 68, 0.3);
  border-radius: 16px;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(68, 170, 68, 0.1) 0%, transparent 70%);
    animation: peace-glow 4s ease-in-out infinite;
  }

  .peace-icon-wrapper {
    position: relative;
    margin-bottom: 16px;

    .peace-icon {
      font-size: 48px;
      color: #66cc88;
      filter: drop-shadow(0 0 15px rgba(102, 204, 136, 0.5));
      animation: peace-float 3s ease-in-out infinite;
    }

    .peace-glow {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80px;
      height: 80px;
      background: radial-gradient(circle, rgba(102, 204, 136, 0.3) 0%, transparent 70%);
      border-radius: 50%;
      animation: peace-pulse 2s ease-in-out infinite;
    }
  }

  .peace-title {
    font-size: 22px;
    font-weight: bold;
    color: #66cc88;
    text-shadow: 0 0 20px rgba(102, 204, 136, 0.5);
    margin-bottom: 8px;
    position: relative;
    z-index: 1;
  }

  .peace-subtitle {
    font-size: 13px;
    color: var(--text-secondary);
    font-style: italic;
    position: relative;
    z-index: 1;
  }
}

@keyframes peace-glow {
  0%, 100% {
    transform: translate(-50%, -50%) rotate(0deg);
    opacity: 0.5;
  }
  50% {
    transform: translate(-50%, -50%) rotate(180deg);
    opacity: 0.8;
  }
}

@keyframes peace-float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes peace-pulse {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.5;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.3);
    opacity: 0.2;
  }
}

