        &.status-无伤 {
          background: rgba(68, 170, 68, 0.15);
          color: #44aa44;
        }

        &.status-轻伤 {
          background: rgba(255, 204, 0, 0.15);
          color: #ffcc00;
        }

        &.status-重伤 {
          background: rgba(255, 136, 0, 0.15);
          color: #ff8800;
        }

        &.status-濒死 {
          background: rgba(255, 68, 68, 0.15);
          color: #ff4444;
          animation: pulse-danger 1.5s ease-in-out infinite;
        }
      }
    }
  }
}

// 修仙提示
.cultivation-tips {
  padding: 16px;
  background: linear-gradient(135deg, rgba(255, 200, 100, 0.08) 0%, rgba(255, 180, 80, 0.05) 100%);
  border: 1px dashed rgba(255, 200, 100, 0.3);
  border-radius: 12px;

  .tip-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    color: #ffc864;

    i {
      font-size: 14px;
    }
  }

  .tip-content {
    font-size: 13px;
    color: var(--text-secondary);
    line-height: 1.6;
    font-style: italic;
  }
}

// 空状态提示
.empty-hint {
  text-align: center;
  padding: 20px;
  color: #506070;
  font-style: italic;
}

// ==================== 手机端响应式适配 ====================
@media screen and (max-width: 480px) {
  // 渡劫面板手机端适配
  .section-header.tribulation-header {
    font-size: 14px;
    margin-bottom: 12px;
    padding-bottom: 10px;
  }
  
  .tribulation-overview {
    padding: 16px;
    gap: 12px;
    
    .tribulation-type-badge {
      padding: 8px 14px;
      font-size: 15px;
      
      i {
        font-size: 16px;
      }
    }
    
    .tribulation-level {
      font-size: 14px;
      padding: 6px 12px;
    }
  }
  
  .tribulation-progress-section {
    padding: 12px;
    
    .section-title {
      font-size: 12px;
    }
    
    .tribulation-stages {
      font-size: 14px;
    }
    
    .tribulation-progress-bar {
      height: 8px;
    }
  }
  
  .tribulation-endurance-section {
    .section-title {
      font-size: 12px;
    }
    
    .endurance-bar-container {
      .endurance-bar {
        height: 12px;
      }
      
      .endurance-value {
        font-size: 13px;
        min-width: 50px;
      }
    }
  }
  
  .protection-section {
    .section-title {
      font-size: 12px;
    }
    
    .protection-list {
      .protection-tag {
        padding: 4px 10px;
        font-size: 11px;
      }
    }
  }
  
  .tribulation-desc-section {
    padding: 12px;
    
    .section-title {
      font-size: 12px;
    }
    
    .tribulation-desc {
      font-size: 12px;
    }
  }
  
  .tribulation-cause-section {
    padding: 10px 12px;
    
    .cause-label {
      font-size: 11px;
    }
    
    .cause-text {
      font-size: 11px;
    }
  }
  
  // 战斗面板手机端适配
  .combat-header-card {
    padding: 12px;
    
    .combat-status-row {
      margin-bottom: 12px;
    }
    
    .combat-status-badge {
      padding: 6px 14px;
      font-size: 13px;
      
      i {
        font-size: 12px;
      }
    }
    
    .combat-round-badge {
      padding: 5px 10px;
      font-size: 11px;
    }
    
    .combat-stats-row {
      padding: 10px 12px;
      gap: 10px;
    }
    
    .stat-item {
      .stat-icon {
        width: 28px;
        height: 28px;
        font-size: 12px;
      }
      
      .stat-bar {
        height: 8px;
      }
      
      .stat-value {
        font-size: 12px;
        min-width: 38px;
      }
      
      .injury-tag {
        padding: 4px 10px;
        font-size: 11px;
      }
    }
  }
  
  .section-label {
    font-size: 12px;
    
    .enemy-count {
      font-size: 11px;
    }
  }
  
  .trump-cards-section {
    .trump-cards-list {
      .trump-card-tag {
        padding: 5px 12px;
        font-size: 11px;
      }
    }
  }
  
  .enemies-section {
    .enemy-card {
      padding: 12px 12px 12px 16px;
      
      .enemy-identity {
        .enemy-name {
          font-size: 13px;
        }
        
        .enemy-realm-tag {
          padding: 2px 8px;
          font-size: 10px;
        }
      }
      
      .enemy-stats {
        gap: 12px;
        
        .enemy-power, .enemy-health {
          font-size: 11px;
          
          i {
            font-size: 10px;
          }
        }
      }
      
      .enemy-trait-row {
        font-size: 10px;
      }
    }
    
    .no-enemy {
      padding: 24px 16px;
      
      i {
        font-size: 24px;
      }
      
      span {
        font-size: 12px;
      }
    }
  }
}
